// tạo mang chứa các số bất kì để render ả sản phâm bất kỳ
export const randomProduct = (length, dataLength, id) => {
    const c = [];
    let b;
    do {
        b = Math.floor(Math.random() * dataLength);
        if (typeof id === "number") {
            if (!c.includes(b) && b !== id) c.push(b);
        } else {
            if (!c.includes(b)) c.push(b);
        }
    } while (c.length < length);
    return c;
};

// tìm kiếm thẻ cha, hiện tượng sủi bỏi
export const getParent = (element, selector) => {
    while (element.parentElement) {
        if (element.parentElement.matches(selector)) {
            return element.parentElement;
        }
        element = element.parentElement;
    }
};

// chuyển đôi chữ vd khuôn sắt => khuon_sat
export const removeVN = (str) => {
    if (str) {
        str = str.replace(/à|á|ạ|ả|ã|â|ầ|ấ|ậ|ẩ|ẫ|ă|ằ|ắ|ặ|ẳ|ẵ/g, "a");
        str = str.replace(/è|é|ẹ|ẻ|ẽ|ê|ề|ế|ệ|ể|ễ/g, "e");
        str = str.replace(/ì|í|ị|ỉ|ĩ/g, "i");
        str = str.replace(/ò|ó|ọ|ỏ|õ|ô|ồ|ố|ộ|ổ|ỗ|ơ|ờ|ớ|ợ|ở|ỡ/g, "o");
        str = str.replace(/ù|ú|ụ|ủ|ũ|ư|ừ|ứ|ự|ử|ữ/g, "u");
        str = str.replace(/ỳ|ý|ỵ|ỷ|ỹ/g, "y");
        str = str.replace(/đ/g, "d");
        str = str.replace(/À|Á|Ạ|Ả|Ã|Â|Ầ|Ấ|Ậ|Ẩ|Ẫ|Ă|Ằ|Ắ|Ặ|Ẳ|Ẵ/g, "A");
        str = str.replace(/È|É|Ẹ|Ẻ|Ẽ|Ê|Ề|Ế|Ệ|Ể|Ễ/g, "E");
        str = str.replace(/Ì|Í|Ị|Ỉ|Ĩ/g, "I");
        str = str.replace(/Ò|Ó|Ọ|Ỏ|Õ|Ô|Ồ|Ố|Ộ|Ổ|Ỗ|Ơ|Ờ|Ớ|Ợ|Ở|Ỡ/g, "O");
        str = str.replace(/Ù|Ú|Ụ|Ủ|Ũ|Ư|Ừ|Ứ|Ự|Ử|Ữ/g, "U");
        str = str.replace(/Ỳ|Ý|Ỵ|Ỷ|Ỹ/g, "Y");
        str = str.replace(/Đ/g, "D");
        // Some system encode vietnamese combining accent as individual utf-8 characters
        // Một vài bộ encode coi các dấu mũ, dấu chữ như một kí tự riêng biệt nên thêm hai dòng này
        str = str.replace(/\u0300|\u0301|\u0303|\u0309|\u0323/g, ""); // ̀ ́ ̃ ̉ ̣  huyền, sắc, ngã, hỏi, nặng
        str = str.replace(/\u02C6|\u0306|\u031B/g, ""); // ˆ ̆ ̛  Â, Ê, Ă, Ơ, Ư
        // Remove extra spaces
        // Bỏ các khoảng trắng liền nhau
        str = str.replace(/ + /g, " ");
        str = str.replace(/\s/g, "_");
        str = str.trim();
        // Remove punctuations
        // Bỏ dấu câu, kí tự đặc biệt
        // str = str.replace(/!|@|%|\^|\*|\(|\)|\+|\=|\<|\>|\?|\/|,|\.|\:|\;|\'|\"|\&|\#|\[|\]|~|\$|_|`|-|{|}|\||\\/g," ");
        return str;
    }
    return str;
};

// chuyển dổi số 1200000 => 1.200.000
export const convertPrice = (price) => {
    if (price) {
        const priceArray = price.split("");
        const priceLength = Math.floor(priceArray.length / 3);
        let thousands = [];
        let hundred = [];

        // đi qua từ cụm số gồm 3 số lần lượt
        for (var i = 0; i <= priceLength; i++) {
            if (i === 0) {
                const a = priceArray.slice(-3 * (i + 1)).join("");
                thousands.push(a);
            }

            if (0 < i && i <= priceLength) {
                const a = priceArray.slice(-3 * (i + 1), -3 * i).join("");
                hundred.push(a);
            }
        }

        // lọc bỏ hết các phần tử rỗng '' trong mảng
        const hundreds = hundred.filter((i) => {
            return i !== "";
        });

        // thêm 1 phần tử rỗng '' vào đầu mảng để khi nối phần tử trong mảng có 1 phần tử trống ở cuối tạo thêm dấu .
        hundreds.unshift("");

        // chuối số cuối cùng
        let priceCovenrt = `${hundreds.reverse().join(".")}${thousands.join()}`;
        return priceCovenrt;
    }
    return price;
};

// code đon Hàng
const text = [
    "A",
    "B",
    "C",
    "D",
    "E",
    "G",
    "H",
    "I",
    "K",
    "L",
    "M",
    "N",
    "O",
    "P",
    "Q",
    "R",
    "T",
    "U",
    "V",
    "X",
    "Y",
];
const num = [1, 2, 3, 4, 5, 6, 7, 8, 9];

const indexText = (arr) => {
    let output = [];
    for (let i = 0; i < 2; i++) {
        const numRandom = Math.floor(Math.random() * arr.length);
        output = [...output, numRandom];
    }

    const resultText = output.reduce((val, item) => {
        val = val + arr[item];
        return val;
    }, "");

    return resultText;
};

export const codeQr = () => {
    const arrText = indexText(text);
    const arrNum = indexText(num);
    return arrText + arrNum;
};
